import {Dashboard} from 'dashboard/dashboard'
import {LayoutWidget} from 'dashboard/lib/entities'
import {MyInfo} from '../widgets/my-info'
import {MyService} from '../widgets/my-service'
import {SocialConnector} from '../widgets/social-connector'

const widgetTypes = {
  MyInfo,
  MyService,
  SocialConnector,
}

const configuration = {
  widget: {
    type: LayoutWidget.verticalList,
    widgets: [
      {type: 'SocialConnector'},
      {
        type: LayoutWidget.horizontalList,
        widgets: [
          {type: 'MyInfo'},
          {type: 'MyService'},
        ],
      },
    ],
  },
}

export const UserInfo = () => <Dashboard configuration={configuration} widgetTypes={widgetTypes} />
